<template>
  <div class="container">
    <div class="my-3">
      <b-button
        href="#"
        variant="primary"
        style="min-width: 80px;"
        @click="editable=!editable"
      >{{ editable?"取消":"编辑" }}</b-button>
      <b-button
        href="#"
        variant="danger"
        style="min-width: 80px;"
        class="mx-3"
        :disabled="!editable"
        @click="saveConfig"
      >保存</b-button>
      <p class="d-inline">谨慎使用，除非你知道到底在做什么</p>
    </div>
    <b-form-textarea
      id="textarea"
      v-model="config"
      placeholder="Enter something..."
      rows="50"
      max-rows="200"
      :disabled="!editable"
    ></b-form-textarea>
  </div>
</template>
<script>
export default {
  data() {
    return {
      config: "default info",
      editable: false
    };
  },
  created: function() {
    let _this = this;
    this.common.axios.get("./admin/ajax/setting").then(e => {
      console.log(e.data);
      _this.config = JSON.stringify(e.data, null, 2);
    });
  },
  methods: {
    saveConfig() {
      let c;
      try {
        c = JSON.parse(this.config);
        if (!c.G_CONFIG || !c.DRIVE_MAP) return alert("invalid config");
      } catch (e) {
        return alert("invalid json");
      }
      this.common.axios
        .post("./admin/ajax/save", c)
        .then(r => {
          alert(r.data);
        })
        .catch(e => {
          console.log(e);
          alert("failed");
        });
    }
  }
};
</script>